@import "../../CommonFiles/commonStyles.module";

.list {
  padding: 0 0 0 6px;
  @include onHoverBackgroundColor;
  @include onActiveBackgroundColor;

  &:hover {
    border-left: 3px solid #4267b2;
  }

  .link {
    display: inline-block;
    min-width: 150px;
    color: $mainTextColor;
    font-size: 14px;
    line-height: 32px;
  }
}
