.statusWrap {
  display: flex;
  width: 50%;
  justify-content: left;
  margin-top: 20px;

  .status {
    display: inline-flex;
    min-height: 20px;
    max-width: 50%;
    font-size: 20px;
    text-align: center;
    padding: 5px 10px;
  }
}

.statusInput {
  border: 1px solid #000000;
  font-size: 20px;
  width: 50%;
  text-align: center;
}
